.sales-page {
    width: 100%;
}

.performance-overview-top {
    display: flex;
    gap: var(--spacing-2xl);
    padding: var(--spacing-lg) var(--spacing-xl);
    margin-bottom: var(--spacing-lg);
}

.perf-item {
    display: flex;
    gap: var(--spacing-md);
    align-items: center;
}

.perf-item .value {
    font-size: 1.5rem;
    font-weight: 700;
}

.sales-layout {
    display: grid;
    grid-template-columns: 1fr 360px;
    gap: var(--spacing-lg);
}

.main-sales-content {
    display: flex;
    flex-direction: column;
    gap: var(--spacing-lg);
}

.rank-progression-v2 {
    padding: var(--spacing-lg);
}

.current-rank-card {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: var(--spacing-xl);
    margin: var(--spacing-lg) 0;
}

.rank-title {
    font-size: 2.5rem;
    color: var(--accent);
}

.rank-progress-circle {
    width: 80px;
    height: 80px;
    border-radius: 50%;
    background: conic-gradient(var(--accent) 65%, transparent 0);
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
}

.circle-inner {
    width: 64px;
    height: 64px;
    background: var(--bg-dark);
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: 700;
}

.next-rank-path {
    background: rgba(255, 255, 255, 0.02);
    padding: var(--spacing-lg);
    border-radius: var(--border-radius-md);
    margin-bottom: var(--spacing-xl);
}

.progress-bar-v2 {
    height: 8px;
    background: rgba(255, 255, 255, 0.05);
    border-radius: var(--border-radius-full);
    margin: 12px 0;
    overflow: hidden;
}

.progress-fill {
    height: 100%;
    background: var(--accent);
}

.rank-scroller {
    display: flex;
    gap: var(--spacing-md);
    overflow-x: auto;
    padding-bottom: var(--spacing-md);
}

.rank-card-v2 {
    min-width: 200px;
    padding: var(--spacing-md);
    background: rgba(255, 255, 255, 0.02);
    border-radius: var(--border-radius-md);
    border: 1px solid var(--glass-border);
    opacity: 0.5;
}

.rank-card-v2.active {
    opacity: 1;
    border-color: var(--accent);
    background: rgba(41, 151, 255, 0.05);
}

.rank-card-v2.completed {
    opacity: 1;
}

.rank-badge-v2 {
    width: 24px;
    height: 24px;
    background: var(--secondary);
    color: #000;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 0.75rem;
    font-weight: 800;
    margin-bottom: var(--spacing-sm);
}

.rank-card-v2.completed .rank-badge-v2 {
    background: var(--fintech-green);
}

.rank-meta h4 {
    font-size: 0.9rem;
}

.commission {
    display: block;
    font-size: 0.75rem;
    color: var(--accent);
    margin: 4px 0;
}

.qual-desc {
    font-size: 0.7rem;
    color: var(--secondary);
}

.sales-sidebar {
    display: flex;
    flex-direction: column;
    gap: var(--spacing-lg);
}

.affiliate-links,
.team-stats {
    padding: var(--spacing-lg);
}

.link-box {
    padding: var(--spacing-md);
    margin-top: 12px;
}

.link-box code {
    display: block;
    font-size: 0.7rem;
    margin: 8px 0;
    color: var(--secondary);
    word-break: break-all;
}

.copy-btn {
    position: absolute;
    top: 12px;
    right: 12px;
    color: var(--secondary);
}

.leg-stats {
    display: flex;
    justify-content: space-between;
    margin: var(--spacing-lg) 0;
}

.leg {
    display: flex;
    flex-direction: column;
    gap: 4px;
}

.flush-timer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: var(--spacing-md);
    border-top: 1px solid var(--glass-border);
}

.timer {
    font-family: var(--font-mono);
    color: var(--fintech-red);
    font-weight: 700;
}

@media (max-width: 1024px) {
    .sales-layout {
        grid-template-columns: 1fr;
    }
}